.App-header {
  min-height: 5vh;
  width: 100%;
  position: fixed;
  padding: 1vh 1vw;

  /* Century Gothic Pro Bold */
  font-family: century-gothic,sans-serif;
  font-weight: 700;
  font-style: normal;
}

.App-body {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.App-body h1 {

  /* Century Gothic Pro Bold */
  font-family: century-gothic,sans-serif;
  font-weight: 700;
  font-style: normal;

  /* Century Gothic Pro Bold Italic */
  /* font-family: century-gothic, sans-serif;
  font-weight: 700;
  font-style: italic; */

  font-size: calc(32px + 1vmin);
}

.section-header{
  width: 100%;
  height: 4em;
  padding-top: 1em;
  padding-bottom: 1em;
  display: block;
  text-align: center;
  font-size: 32px;
}
.section-header h1 {
  /* Century Gothic Pro Bold */
  font-family: century-gothic,sans-serif;
  font-weight: 700;
  font-style: normal;
}

.section-hero {
  width: 100%;
  height: 100vh;

  /* 子要素を上下中央にセンタリング */
  display: flex;
  justify-content: center;
  align-items: center;
}
.section-hero::before {
  /* 背景画像の設定 */
  background-image: url("./img/background_hero_FHD_50.jpg");
	background-position: top center;
	background-repeat: no-repeat;
  background-size: cover;
  
  position: fixed;
  content: '';
  display: block;
  width: 100%;
  height: 100vh;

  z-index: -1;
}

.section-about-me {
  width: 100%;
  min-height: 100vh;
}


.section-about-me-body{
  width: 100%;
  height: 100%;
  display: flex;
  
  /* 子要素を上下中央にセンタリング */
  display: flex;
  justify-content: center;
  align-items: center;
}
.about-me {
  min-width: 40vw;
  min-height: 50vh;

  display: block;
  
  background-color: rgba(255, 255, 255, 0.6);
  margin-right: auto;
  margin-left: auto;

  padding: 5vw;
}
.about-me img {
  width: 50vw;
  max-width: 300px;
}
.about-me p {
  color: #000000;
  display: block;
}

.section-sns {
  width: 100%;
  min-height: 100vh;
}

.section-sns-body{
  width: 100%;
  height: 100%;
  
  /* 子要素を上下中央にセンタリング */
  display: flex;
  justify-content: center;
  align-items: center;

  display: flex;
  flex-wrap: wrap;
}

.Snspace {
  width: 400px;
  height: 100%;
  margin-top: 5vh;
  margin-bottom: 5vh;
  margin-left: 1vw;
  margin-right: 1vw;
  padding-top: 5vh;
  padding-bottom: 5vh;
  /* background-color: rgba(255, 255, 255, 0.6); */
  text-align: center;
}

.Snspace img {
  width: 50vw;
  max-width: 300px;
}

.Snspace .discribe-space {
  /*  */
  background-color: rgba(255, 255, 255, 0.6);
}

.Snspace h2 {
  /* Century Gothic Pro Bold */
  font-family: century-gothic,sans-serif;
  font-weight: 700;
  font-style: normal;

  /* font-size: calc(18px + 1vmin);   */
  font-size: 24px;
  margin: 0;

}

.Snspace .p-caption {
  /* Century Gothic Pro Bold */
  font-family: century-gothic,sans-serif;
  font-weight: 700;
  font-style: normal;

  /* font-size: calc(8px + 1vmin); */
  font-size: 12px;
  margin: 0;
}

.Snspace .p-discribe {
  /* font-size: calc(8px + 1vmin); */
  font-size: 12px;
  font-weight: bold;
  margin: 0;
}

.Snspace a {
  color: #000000;
  
  /* Century Gothic Pro Bold */
  font-family: century-gothic,sans-serif;
  font-weight: 700;
  font-style: normal;

  /* font-size: calc(10px + 1vmin); */
  font-size: 18px;
}

.App-footer {
  background-color: #282c34;
  min-height: 30vh;
  width: 100%;
}